.projects {
    padding-bottom: 2rem;
  
    &__bio-image {
      height: 30vh;
      width: 100%;
      padding-top: 48vh;
      background: linear-gradient(
          to right,
          rgba($primary-color, 0.9),
          rgba($primary-color, 0.3)
        ),
        url("./img/hacking.jpg") center center;
      background-size: cover;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-end;
      @include MEDIUM-DEVICE-WIDTH {
        height: 40vh;
      }
  
      .text-secondary {
        font-size: 2rem;
        margin-bottom: 0.2rem;
      }
    }
  
    &__items {
      padding: 0 1rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-gap: 2rem;
      @include MEDIUM-DEVICE-WIDTH {
        grid-template-columns: repeat(2, 1fr);
      }
    }
  
    &__item {
      max-height: 60vh;
      width: 100%;
      position: relative;
      border-bottom: 5px solid $secondary-color;
      overflow: hidden;
      cursor: pointer;
      @include MEDIUM-DEVICE-WIDTH {
        display: flex;
        justify-content: center;
        align-items: center;
      }
  
      img {
        width: 100%;
      }
      &::after {
        content: "";
        position: absolute;
        top: 100%;
        left: 0;
        height: 100%;
        width: 100%;
        background: $secondary-color;
        opacity: 0;
        @include transition-ease;
      }
  
      &:hover {
        &::after {
          top: 0;
          opacity: 0.77;
        }
        .projects__btn {
          opacity: 1;
        }
      }
    }
  
    &__btns {
      position: absolute;
      top: 50%;
      width: 100%;
      height: 20px;
      z-index: 1;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
      text-align: center;
    }
  
    &__btn {
      opacity: 0;
      color: set-text-color($secondary-color);
      @include transition-ease;
  
      &:hover {
        color: set-text-color(set-text-color($secondary-color));
        font-size: 1.3rem;
      }
    }
  
    .social-icons {
      display: flex;
      flex-direction: column;
    }
  
    footer {
      transform: rotate(90deg) translate(-3rem, -5rem);
    }
  }
  