.home {
    height: 100vh;
    padding-top: 40vh;
    overflow: hidden;
    align-items: center;
    text-align: center;
    background: 
        linear-gradient(
            to right,
            rgba($primary-color, 0.9),
            rgba($primary-color, 0.3)
        ),
        url('./img/weave.jpg') center top;
    background-size: cover;

    h2 {
        text-transform: capitalize;
    }

    &__name {
        font-size: 2.8rem;
        padding-bottom: 1rem;
        margin-bottom: 1rem;
        line-height: 3.75rem;
        border-bottom: 2px solid set-text-color($primary-color);

        &--last {
            font-weight: 700;
            color: $secondary-color;
        }

    }
}

